@keyframes growShrink {

  0%,
  100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }
}

.grow-shrink {
  animation: growShrink 1s ease-in-out infinite;
}

.gif {
  width: 100%;
  animation: fadeOut 60s infinite;
  /* Animation that runs every 60 seconds */
}

@keyframes fadeOut {

  0%,
  99.9% {
    opacity: 1;
    /* Fully visible */
  }

  100% {
    opacity: 0;
    /* Fully hidden */
  }
}